<template>
<main id="app">
  <p>
    {{ width }} &times; {{ height }}
  </p>
  <p>
    <input type="text" placeholder="Only for trigger virtual keyboard"/>
  </p>
</main>
</template>

<script>
export default {
  data: () => ({
    width: 0,
    height: 0,
  }),
  mounted() {
    const ro = new ResizeObserver(this.onResize);
    ro.observe(document.documentElement);
  },
  methods: {
    onResize(entries) {
      const [{ contentRect }] = entries;
      const { width, height } = contentRect;
      this.width = width;
      this.height = height;
    },
  },
};
</script>

<style>
html, body {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

main {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 3em;
  height: 100%;
}

input {
  padding: 10px;
}
</style>



